<template>
  <div>   
<div v-if="onDosCarte" class="box"> 

<img v-for="dosCar in dosCarte" :key="dosCar" v-on:click="setActiveCarte(dosCar, $event);" :id="dosCar" class="size-carte-dos" :src="imageUrl(dosCar, niveau)" :style="styleA(dosCar,carte,niveau)"    />
   </div>

<div class="btn-dos-cartes" v-on:click="setOnDosCarte();  setOnNewCarte();" >
    <img  class="carte-btn-dos-cartes"  :src="urlCarteActive" />
    </div>
    <div v-if="onNewCarte" class="new-dos-cartes">nouveau</div> 
</div>
</template>

<script>
export default {
  name: 'DosCartes',
  props: ['niveau', 'carte', 'dosCarte'],
computed:{
      onDosCarte(){
     return this.$store.state.onDosCarte;
    },
        urlCarteActive(){
     return "img/card-"+this.$store.state.carteActive+".png" ;
    },

    onNewCarte(){
     return this.$store.state.onNewCarte;
    },

    },
   methods:{
    
    imageUrl: function (nom, niveau) {
      console.log(nom);
      if(nom==0){ 
        
        return "img/card-0.png";}
        else if(nom==1&&niveau>=5){ 
  
          return "img/card-1.png";}
      else {return "img/card-off.png";}

    },
    styleA: function (nom,carteActive,niveau) {
    
      if(nom==carteActive){ 
        
        return "pointer-events:auto;width:12%;margin: 1.9% 1.9%;border:2px solid #f5c74e;background-color:#f5c74e;border-radius:5px";
        
        }
        if(niveau<5&&nom>0){ 
        
          return "pointer-events:none";
          
          }
          if(niveau<10&&nom>1){ 
        
            return "pointer-events:none";
            
            }
            if(niveau<100&&nom>1){ 
        
              return "pointer-events:none";
              
              }

      
       
         
       

    },
    setActiveCarte: function (numCarte, event) {
      

      if (event) {
        
       
       this.$emit('set_carte', numCarte)
       
      } 
      
    },
    setDosCarte: function (num) {
   
          this.$store.commit('setCarteActive', num);
      
        },

    setOnDosCarte: function () {
            var bool = "";
            if (this.onDosCarte) {bool=false;}else {
              this.$store.commit('setOnInfoNiveau', false);
              bool=true;}
              
             this.$store.commit('setOnDosCarte', bool);
    
        },
    setOnNewCarte(){
     this.$store.commit('setOnNewCarte', false);
    },

  },

}
</script>

