import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    id:1,
    niveau: 1,
    nbrCarte: 4,
    win: 0,
    bad: 0,
    vie:100,
    temps:100,
    time:0,
    onGame: false,
    onDosCarte: false,
    onNewCarte: false,
    onInfoNiveau: true,
    carte: [],
    dosCarte: [],
    titre:"Memoire Flash",
    essai1: "",
    timeInOff:"-off",
    vieInOff:"-off",
    stopTimer:0,
    carteActive:0,
    message:"",
  },
  mutations: {
    addCartes(state, num){
      
      state.carte=[];
      var r=Math.floor(Math.random() * ((11-num) - 0 + 1) + 0);
     
      for (var i=0; i<num;i++){
        state.carte.push(0+""+(i+r));
        state.carte.push(1+""+(i+r));
    }
    
    },
    setEssai1(state, num){
      
      state.essai1=num;
    
 
    },
    setVie(state, num){
      
      state.vie=num;
    
 
    },
    setTemps(state, num){
      
      state.temps=num;
    
 
    },
    setMessage(state, string){
      
      state.message=string;
    
 
    },
    setTime(state, num){
      
      state.time=num;
    
 
    },
    setId(state, num){
      
      state.id=num;
    
 
    },
    setOnGame(state, bool){
      
      state.onGame=bool;
    
    },
    setOnDosCarte(state, bool){
      
      state.onDosCarte=bool;
    
    },
    setOnNewCarte(state, bool){
      
      state.onNewCarte=bool;
    
    },
     setOnInfoNiveau(state, bool){
      
      state.onInfoNiveau=bool;
    
    },
    setStopTimer(state, num){
      
      state.stopTimer=num;
    
 
    },
    setVieInOff(state, num){
      
      state.vieInOff=num;
    
 
    },
    setTimeInOff(state, num){
      
      state.timeInOff=num;
    
 
    },
    setBad(state, num){
      
      state.bad=num;
    
 
    }
    ,setWin(state, num){
      
      state.win=num;
    
 
    },
    setDosCarte(state, num){
      
      state.dosCarte=[];
      
      for (var i=0; i<num;i++){
        state.dosCarte.push(i);
   
    }
    },
    setCarteActive(state, num){
      
      state.carteActive=num;
   
    },
    shuffleCartes: function (state, array) {
      var currentIndex = array.length, temporaryValue, randomIndex;
    
      // While there remain elements to shuffle...
      while (0 !== currentIndex) {
    
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
    
        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }
    
      state.carte= array;
    },
    resetGame: function(state, n){

      if(n){
        state.onGame=false;state.win=0;
        state.carte=[];state.niveau++;state.bad=0;
        state.vie=100;state.essai1="";
        state.timeInOff="-off";state.vieInOff="-off";
        state.stopTimer=0;state.time=0;state.temps=100;
        if(state.niveau==11||state.niveau==21)state.nbrCarte++;
        if(state.niveau==5)state.onNewCarte=true;
      }else{       
        state.onGame=false;state.win=0;state.bad=0;state.vie=100;
        state.carte=[];state.essai1="";state.timeInOff="-off";
        state.vieInOff="-off";state.stopTimer=0;state.time=0;state.temps=100;
      
      }

    },



  },
  actions: {
  },
  modules: {
  }
})
