<template>
  <div  class="home" unselectable="on" onselectstart="return false;" onmousedown="return false;" >
     
<InfoNiveau   :niveau="niveau"   />
  
 
  

 <DosCartes :dosCarte="dosCarte"  :niveau="niveau" :carte="carteActive"  />


 

  </div>

</template>

<script>
// @ is an alias to /src
import DosCartes from '@/components/DosCartes.vue'
import InfoNiveau from '@/components/InfoNiveau.vue'

export default {
  name: 'Home',
  components: {
    DosCartes,
    InfoNiveau
  },
  computed:{
    id(){
   
     return this.$store.state.id;
    },
    niveau(){
      
     return this.$store.state.niveau;
    },
    dosCarte(){
     return this.$store.state.dosCarte;
    }
    ,
    carteActive(){
     return this.$store.state.carteActive;
    },

    
    
  },

          beforeMount(){
          if (this.id==0) {
          this.$router.push('/login');  
          }else{
          this.clearAllInterval();
         this.createDosCarte(); 
         }
        },

methods:{  




        createDosCarte:function(){
          
          this.$store.commit('setDosCarte', 12);
      
        },
             clearAllInterval: function(){

     
    var interval_id = window.setInterval("", 9999); // Get a reference to the last
                                                
for (var i = 0; i < interval_id; i++) {
        clearInterval(i);}
      
    },
       
       },

}



</script>
