<template>

 <div >  
   <div v-if="onInfoNiveau" class="box">  
    <div  class="box-Info-niveau" :style="'border-color:'+getColor"  >
      <div  class="titre-Info-niveau"  :style="'background-color:'+getColor" >Niveau {{getDifficulte}}</div>
      <div v-for="getM in getMessage" :key="getM.message" class="message-Info-niveau"   >{{getM.message}}</div><br/>
   <router-link to="/game"  class="btn_play" ><b>commencer la partie</b></router-link>
  </div>
   </div>
  <div v-on:click="setOnInfoNiveau()"   class="btn-niveau " :style="'border-color:'+getColor" > <div class="number-btn-niveau" >{{ niveau }}</div></div>   
</div>
</template>

<script>
export default {
  name: 'InfoNiveau',
  props: ['niveau' ],
   computed:{

   

     getColor(){
      var d=this.getDifficulte;
          var text="";
         switch (d) {


  case "facile":  
    text = "#f5c74e";
        break;
          case "moyen":  
    text = "#f7a132;color:#fff!important;";
        break;
          case "difficile":  
    text = "#ed6638;color:#fff!important;";
        break;
                  case "super difficile":  
    text = "#e94236;color:#fff!important;";
        break;

  default:
    text = "#f5c74e";

    }
    return text;
      },
     getDifficulte(){

       var text="";
       var n =this.niveau+"";
       
       n=n.substring(n.length-1);
       n=parseInt(n, 10);
       switch (n) {

  case 1:
  case 2:
  case 3:  
    text = "facile";
    break;
  case 4:
  case 5:  
  case 6:
  case 7:  
    text = "moyen";
    break;
    case 8:  case 9:
    text = "difficile";
    break;
     case 0:
    text = "super difficile";
    break;
  default:
    text = "facile";

}
      return text;

     },
          getCoups(){

       var text="";
       var n =this.niveau+"";
       
       n=n.substring(n.length-1);
       n=parseInt(n, 10);
       switch (n) {



    case 9: case 0:
    text = "5";
    break;
  default:
    text = "10";

}
      return text;

     },



     
     getCartesSupp(){

       var text="";
       var n =this.niveau+"";
       
       n=n.substring(n.length-1);
       n=parseInt(n, 10);
       switch (n) {

  case 1:
  case 3:  
  case 5:  
  case 7:  
  case 9:  
    text = "non";
  break;
  default:
    text = "oui";

}
      return text;

     },
     
     getTemps(){

       var text="";
       var n =this.niveau+"";
       
       n=n.substring(n.length-1);
       n=parseInt(n, 10);
       switch (n) {

  
  case 5:  
  case 6:
  case 7:
  case 8:  

    text = "30s";
      break;
  case 9:
    text = "20s"; 
      break;
  case 0:
    text = "15s";
  break;
  default:
    text = "illimités";

}
      return text;

     },

     getMessage(){
      var array=[];
      array.push({message:"Cartes : "+(this.$store.state.nbrCarte*2)},{message:"Cartes supprimés : "+this.getCartesSupp} ,{message:"Coups : "+this.getCoups} ,{message:"Temps : "+this.getTemps} );
      return array;
     
     },
       
    

onInfoNiveau(){
     return this.$store.state.onInfoNiveau;
    },

  },
  
  methods:{
    
      setOnInfoNiveau: function () {
            var bool = "";
            if (this.onInfoNiveau) {bool=false;}else {
              this.$store.commit('setOnDosCarte', false);
              bool=true;}
              
             this.$store.commit('setOnInfoNiveau', bool);
    
        },


  },

}
</script>

